/*jslint indent: 2*/

/*global require: true, module: true*/
(function () {
  'use strict';

  module.exports = {
    '+': function _(value) {
      return ['sum', ['add', value]];
    },
    '-': function _(value) {
      return ['sum', ['subtract', value]];
    },
    '*': function _(value) {
      return ['sum', ['multiply', value]];
    },
    '/': function _(value) {
      return ['sum', ['divide', value]];
    },
    'b': function b(value) {
      return [['best-of', value], 'sum'];
    },
    'w': function w(value) {
      return [['worst-of', value], 'sum'];
    }
  };
})();